.App {
  height: 100%;
  width: 100%;
  background-color: #5d5c5c;
}

.main-screen {
  display: grid;
  grid-template-areas:
    "left header right"
    "left game right"
    "left control right";
  grid-template-columns: 1fr 480px 1fr;
  margin: 0;
  padding: 0;
}

@media (max-width: 480px) {
  .main-screen {
    grid-template-columns: 0px 100vw 0px;
  }
}

.left {
  grid-area: left;
  height: 100vh;
}

.right {
  grid-area: right;
  height: 100vh;
}

.header {
  grid-area: header;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: #282828;
}

#myCanvas {
  grid-area: game;
  width: 100%;
  height: 100%;
}

.camera {
  display: flex;
  justify-content: center;
  align-items: center;
}

#video {
  margin: 25px;
  width: calc(100% - 100px);
  height: auto;
}

.controls {
  grid-area: control;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #282828;
}

.control-buttons {
  background-color: #282828;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 25px;
}